.center {
  position: relative;
  width: 20px;
  height: 20px;
  background-color: none;
}

.center::before,
.center::after {
  content: "";
  position: absolute;
  background-color: red;
}

.center::before {
  width: 2px;
  height: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.center::after {
  width: 50%;
  height: 2px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.max::before,
.max::after {
  background-color: rgb(0,255,255);
}